@tailwind base;
@tailwind components;
@tailwind utilities;
*{
  transition: .1s;
  scroll-behavior: smooth;
}
.ts{
  transition: .2s;
}
.bg-gradient{
  /* background-image: linear-gradient(t,#F9D949); */
  background: #333333;
}
.blur{
  filter: blur(3px);
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.gold{
  color: #FFB000;
}
.navigation-item{
  position: relative;
  transition: .2s;
  padding: .5rem 0;
}
.navigation-item::before{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 2px;
  background-color: #FFCC70;
}
.navigation-item::after{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0%;
  height: 2px;
  background-color: #FFCC70;
}
.navigation-item.active::before{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #FFCC70;
}
.navigation-item:hover::before{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 2px;
  background-color: #FFCC70;
}
.navigation-item.active::after{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #FFCC70;
}
.navigation-item:hover::after{
  transition: .2s;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #FFCC70;
}
.shadow4{
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.l50{
  position: absolute;
  bottom: -30px;
}
.dot{
  margin: 0 7.5px;
  width: 10px;
  height: 10px;
  border: 1px solid #FFB000;
  border-radius: 50%;
}
.dot:nth-child(1).active{
  margin: 0 7.5px;
  width: 10px;
  height: 10px;
  background: #FFB000;
  border: 1px solid #FFB000;
  border-radius: 50%;
}
.dot:nth-child(2).active{
  margin: 0 7.5px;
  width: 10px;
  height: 10px;
  background: #FFB000;
  border: 1px solid #FFB000;
  border-radius: 50%;
}
.dot:nth-child(3).active{
  margin: 0 7.5px;
  width: 10px;
  height: 10px;
  background: #FFB000;
  border: 1px solid #FFB000;
  border-radius: 50%;
}


.articleWrapper,
.articleWrapperr{
  position: absolute;
  width: 100%;
  height: 150px;
  top: 50%;
  left: 0;
  bottom: 0;
  transform: translate(0%,150%);
}
.heart{
  top: -100px;
  right: -100px;
}
.wrapper:hover .articleWrapper{
    position: absolute;
    width: 100%;
    height: 150px;
    left: 0;
    bottom: 0;
    transform: translate(0%,75px);
}
.wrapper:hover .articleWrapperr{
  position: absolute;
  width: 100%;
  height: 150px;
  left: 0;
  bottom: 0;
  transform: translate(0%,40px);
}
.wrapper:hover .heart{
  top: 1rem;
  right: 1rem;
}
.item.active{
  border-top:2px solid #FFCC70;
}